export const SPLIT_REGEX = /\W+/g;
export const TEST_REGEX = /(\w\W+|\W+\w)/;
export function getType(item) {
    if (Array.isArray(item)) {
        return "array";
    }
    else if (typeof item === "object" && item) {
        return "object";
    }
    else if (typeof item === "string" && TEST_REGEX.test(item)) {
        return "split";
    }
    else {
        return "leaf";
    }
}
